import{PolymerElement,html}from"@polymer/polymer/polymer-element.js";import{StyleMixin}from"@geodan/gm-style-mixin/build/dist/gm-style-mixin.js";import"./gm-profile-menu.js";import"@geodan/gm-iconset-svg/gm-iconset-svg.js";import"@polymer/iron-icon/iron-icon.js";import"@polymer/paper-tooltip/paper-tooltip.js";import"./gm-profile-panel-style.js";/**
 `gm-profile-panel`
 Webcomponent to retrieve GeodanMaps user gm-profile-panel details.

 @demo demo/index.html
 */class GmProfilePanel extends StyleMixin(PolymerElement){static get template(){var newTemplate=html([`
      <style include="gm-profile-panel-style"></style>
      <div class="login-panel-wrapper">

        <template is="dom-if" if="[[logoUrl]]">
          <div id="logo-container" class="logo-container">
            <template is="dom-if" if="[[allowPageReset]]">
              <img id="logo" class="logo reset" src="[[logoUrl]]" on-click="_reloadPage">
              <paper-tooltip for="logo" class="no-fade" position="bottom" animation-delay="0" offset="4">[[pageResetText]]</paper-tooltip>
            </template>
            <template is="dom-if" if="[[!allowPageReset]]">
              <img class="logo" src="[[logoUrl]]">
            </template>
          </div>
        </template>

        <div class="profile-container" hidden$="[[_isNotLoggedIn]]">
          <iron-icon id="profile-icon" class="profile-icon" icon="gm:user"></iron-icon>
          <paper-tooltip for="profile-icon" class="no-fade" position="bottom" animation-delay="0" offset="12">[[displayName]]</paper-tooltip>
        </div>

        <template is="dom-if" if="{{showInitials}}">
          <div id="profile-info" class="profile-info-container" hidden$="[[_isNotLoggedIn]]">
            <span>[[_initials]]</span>
          </div>
          <paper-tooltip for="profile-info" class="no-fade" position="bottom" animation-delay="0" offset="4">[[displayName]]</paper-tooltip>
        </template>

        <template is="dom-if" if="[[showMenu]]">
          <gm-profile-menu
            menu-title="[[menuTitle]]"
            geodan-maps-logout-url="[[geodanMapsLogoutUrl]]"
            ahp-logout-url="[[ahpLogoutUrl]]"
            logout-title="[[logoutTitle]]"
            is-not-logged-in="[[_isNotLoggedIn]]"
            menu-items="[[menuItems]]">
          </gm-profile-menu>
        </template>
      </div>`]);this.addStylesToTemplate(newTemplate);return newTemplate}static get is(){return"gm-profile-panel"}static get properties(){return{/**Title for menu-button*/menuTitle:{type:String,value:"menu"},/**Url to an image*/logoUrl:{type:String,value:null},/**Add reset link to logo*/allowPageReset:{type:Boolean,value:!1},/**Tooltip text for page reset*/pageResetText:{type:String,value:"Terug naar startsituatie"},/**logout url for GeodanMaps*/geodanMapsLogoutUrl:{type:String,value:"https://services.geodan.nl/sso/sp/Logout"},/**logout url for ahp*/ahpLogoutUrl:{type:String,value:"https://apps.geodan.nl/sso/sp/Logout"},/**title for logout option in menu*/logoutTitle:{type:String,value:"Log out"},/**show initials for user*/showInitials:{type:Boolean,value:!1},/**Name of the user*/displayName:{type:String,value:""},/** computed initials*/_initials:{type:String,computed:"_getInitials(displayName)"},/**show menu button*/showMenu:{type:Boolean,value:!1},/**list of menu-items to show in menu. Items must have a title and an url:'[{"title": "GeodanMaps", "url": "https://www.geodanmaps.nl/" }]'*/menuItems:{type:Array,value:[]},/**user is not logged in*/_isNotLoggedIn:{type:Boolean,computed:"_isUserNotLoggedIn(displayName)",readOnly:!0}}}/// Private methods ---------------------------------------------
// Get the initials for the profile
_getInitials(displayName){let initials="";if(displayName){const names=displayName.split(" ");names.forEach(name=>{if(0<name.length){initials+=name.substring(0,1).toUpperCase()+"."}})}return initials}_reloadPage(){window.location.reload()}_isUserNotLoggedIn(displayName){return""===displayName}}window.customElements.define(GmProfilePanel.is,GmProfilePanel);